.footer {
  position: fixed;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
  height: 90px;
  width: 100%;
  background-color: #181818;
  border-top: 1px solid #282828;
  color: hsla(0, 0%, 100%, .3);
  color: hsla(0, 0%, 100%, .7);
}

.footer__left {
  flex: 0.3;
  display: flex;
  gap: 1rem;
  align-items: center;
}

.footer__left img {
  width: 3.5rem;
  object-fit: contain;
}

.footer__left .song__info .song__name {
  color: #ffffff;
}

.footer__left .song__info .song__artist {
  font-size: 0.8rem;
}

.footer__middle {
  flex: 0.4;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer__middle .control__icon {
  width: 3rem;
  margin: 0 -0.25rem;
}

.footer__right {
  flex: 0.3 1;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  justify-content: flex-end;
  padding: 0 1rem 0 0;
}

.slider {
  width: 100px !important;
  display: block;
}

.icon {
  position: relative;
}

@media (max-width : 726px) {
  .footer__left img {
    width: 2.5rem;
  }

  .footer__left .song__info .song__artist {
    font-size: 0.5rem;
  }

  .footer__middle .control__icon {
    width: 1.5rem;
    margin: 0 0rem;
  }

  .slider {
    width: 60px !important;
    display: block;
  }
}